@media (max-width: 1024px) {
    .add{
        padding: 0px 10px;
    }
    .info {
        padding: 0px 10px;
    }
    .infoMobileApp {
        &__content{
            padding: 0px 20px 0px 10px;
        }
        &__img {
            display: none;
        }
    }
}
@media (max-width: 768px) {
    .add{
        &__slide{
            height: 75px !important;
        }
    }    
    .info {
        &__h4 {
           font-size: 15px;
           margin-bottom: 5px;
        }
        &__text {
           font-size: 10px;
           margin-left: 15px;
           margin-bottom: 5px;
           &_first{
              margin-left: 10px;
           }
        }
        &__h5 {
           font-size: 10px;
           margin-left: 10px;
           margin-bottom: 5px;
        }
        summary::after{
            content: url("../../images/icons/plus12.svg"); 
        }     
     }     
    .infoMobileApp {
        &__container {
           margin-bottom: 20px;
        }
        &__text {
            font-size: 12px;
            width: auto;
            margin-bottom: 20px;
        }
        &__title{
            font-size: 26px;
        }
        &__buttons-box {
           width: 40%;
           height: 80px;
           display: grid;
           gap: 10px;
           margin-bottom: 30px;
        }
        &__button {
           &_big {
              width: auto;
           }
        }
    }

}
@media (max-width: 480px) {
    .add{
        &__slide{
            height: 47px !important;
        }
    }    
    .info {
        margin-bottom: 20px;
        padding: 0px 5px;
        &__h4 {
           font-size: 12px;
           margin-bottom: 5px;
        }
        &__text {
           font-size: 10px;
           margin-left: 10px;
           margin-bottom: 5px;
           &_first{
              margin-left: 5px;
           }
        }
        &__h5 {
           font-size: 10px;
           margin-left: 5px;
           margin-bottom: 5px;
        }
     }  
     .infoMobileApp {
        &__title{
            font-size: 18px;
            margin-bottom: 5px;
        }
        &__text{
            margin-bottom: 10px;
        }
        &__buttons-box {
           width: 50%;
           height: 80px;
           display: grid;
           gap: 10px;
           margin-bottom: 20px;
        }
        &__button {
           &_big {
              width: auto;
           }
        }
    }
   
}