.categories {
margin-bottom: 50px;
&__title {
    font-size: 28px;
    margin-bottom: 20px;
    font-weight: 900;
}
&__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    }
&__items {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 20px;
}
&__item {
    cursor: pointer;
    border-radius: 10px;
    padding: 30px;
    position: relative;
    transition: background-color 0.3s ease;
    width: 153px;
    height: 103px;
    &::before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    z-index: 2;
    background-color: rgba(153, 153, 153, 0.9);
    transition: background-color 0.3s ease;
    }
    &:hover::before{
    background-color: rgba(153, 153, 153, 0.5);
    }
    &_active::before{
        background-color: transparent;
    }
    &_active:hover::before{
    background-color: transparent;
    }
    }
    &__text {
    user-select: none;
    text-align: center;
    color: white;
    font-size: 16px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 3;
}
}
.catalog {
margin-bottom: 50px;
&__title{
    font-size: 28px;
    margin-bottom: 20px;
    font-weight: 900;
}
&__button{
    height: 49px;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    background-color: rgb(242, 242, 246);
    padding: 15px 70px;
    font-weight: 700;
    margin-top: 20px;
    border-radius: 10px;
    transition: background-color 0.3s, transform 0.1s, padding 0.2s;
    &:hover{
        background-color: rgba(242, 242, 246, 0.7);
        padding: 15px 100px;
    }
    }
}
.service {
    position: relative;
    background-color: rgb(242, 242, 246);
    border-radius: 10px;
    padding: 10px;
    &__container{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        align-items: start;
        gap: 20px;      
    }
    &__likeIcon-box{
        position: absolute;
        top: 10px;
        right: 10px;
    }
    &__content {
        @include f(start, center);
        margin-bottom: 30px;
    }
    &__img {
        height: 80px;
        width: 80px;
        background-color: rgb(194, 194, 197);
        border-radius: 10px;
        margin-right: 10px;
    }
    &__text {
        @include f(start, start);
        flex-direction: column;
    }
    &__title {
        margin-bottom: 20px;
    }
    &__address {
    }
    &__bottom {
        @include f(space-between, center);
    }
    &__rating {
    }
    &__button {
        background-color: white;
        color: rgb(27, 139, 245);
        padding: 8px 30px;
        border-radius: 30px;
        font-weight: 600;
        
    }
}
.placeServices {
    background-image: url('../../images/other/подразместитьсвоиуслуги.png');
    background-size: 100% 100%;
    padding: 40px 70px;
    border-radius: 30px;
    background-color: rgb(1, 67, 165);
    @include f(start, center);
    position: relative;
    left: 0;
    right: 0;
    width: calc(100vw - (100vw - 1140px) / 2);
    margin-bottom: 50px;
    &__content {
    color: white;
    }
    &__title {
    font-size: 28px;
    margin-bottom: 30px;
    font-weight: 600;
    }
    &__text {
    font-size: 16px;
    margin-bottom: 25px;
    font-weight: 400;
    line-height: 30px;
    }
    &__button {
    width: 100%;
    padding: 15px 15px;
    font-weight: 700;
    border-radius: 10px;
    transition: background-color 0.3s, transform 0.1s;
    &:hover{
        background-color: $orange;
        color: white;
    }
    }
    &__img {
    }
}
