@media (max-width: 1024px) {
   .header{
         &__container{
            padding: 0px 15px 0px 10px
         }
        &__firstLine{
           @include f(space-between, center);
           margin-bottom: 20px;
        }
        &__about{
           margin-left: 0px;
           a {
            margin-right: 30px;
                &:last-child { 
                    margin-right: 0px;
                }
            }
        }
     
        &__secondLine{
           @include f(space-between, center);
           margin-bottom: 25px;
        }
        &__logo{
           height: 32px;
           object-fit: contain;
        }
        
        &__auth{
           text-wrap: nowrap;
           a{
              text-decoration: underline dotted;
              text-underline-offset: 15px;
           }
           &-slash{
              margin: 0px 10px;
              color: rgb(162, 163, 172);
              font-size: 12px;
           }
        }
        &__search{
           width: 100%;
           padding: 10px;
           padding-left: 15px;
           border: 1px solid rgb(162, 163, 172);
           border-radius: 5px;
           &-box{
              position: relative;
              width: 100%;
              margin-left: 20px;
        
           }
           &::placeholder{
              font-size: 14px;
              color: rgb(162, 163, 172);
           }
           &-iconBox{
              cursor: pointer;
              position: absolute;
              top: 2px;
              right: 2.5px;
              background-color: $blue;
              height: 34px;
              width: 40px;
              border-radius: 5px;
              button{
                 @include f(center, center);
                 background-color: transparent;
                 width: 100%;
                 height: 100%;
              }
           }
        }
     
     
   }
   .footer {
      &__pagesLinks{
         @include f(center, start);
         gap: 20px;
      }
      &__content {
         @include f(center, start);
         gap: 20px;
         margin-bottom: 50px;
         padding: 0px 10px;
      }
      &__contactUs {
         @include f(start, center);
         flex-direction: column;
      }
      &__contactUs-text{
         font-family: "Raleway", sans-serif;
         font-size: 18px;
         margin-bottom: 15px;
      }
      &__contactUs-btn {
         background-color: $blue;
         padding: 8px 15px;
         border-radius: 25px;
         border: none;
         font-size: 12px;
         transition: background-color 0.3s, transform 0.1s;
         &:hover{
            background-color: rgba($color: $blue, $alpha: 0.8);
         }
   
      }
      &__item {
         @include f(start, start);
         flex-direction: column;
         gap: 20px;
         &_apps{
            :first-child{
               margin-right: 30px;
            }
         }
         :first-child{
            margin-bottom: -5px;
         }
      }
      &__buttons-box {
         width: 234.75px;
         max-height: 100.94px;
         display: grid;
         grid-template-columns: 57% 43%;
         grid-template-rows: 1fr 1fr;
         gap: 10px;
         box-sizing: content-box;
         :first-child{
            margin-bottom: 0px;
            margin-right: 0px;
         }
      }
      &__button {
         background-color: rgb(76, 79, 95);
         border-radius: 10px;
         padding: 5px 10px;
         height: auto;
         @include f(center, center);
         transition: background-color 0.3s, transform 0.1s;
         &:hover{
            background-color: rgba($color: rgb(76, 79, 95), $alpha: 0.8);
         }
         &_big{
            padding: 10px 10px;
            grid-row: 1/3;
            grid-column: 2/3;
            &:hover{
               background-color: rgb(76, 79, 95);
            }
         }
      }
      &__hr{
         display: block;
         margin-bottom: 20px;
      }
      &__underLine {
         @include f(space-between, start);
         color: rgb(99, 101, 111);
         a{
            color: rgb(99, 101, 111);
            text-decoration: underline;
         }
      }
      &__item {
         span{
            font-size: 12px;
            letter-spacing: 2px;
         }
         &_underLine{
            gap: 5px;
            a{
               font-size: 12px;
            }
         }
      }
      &__bigLetter{
         font-size: 16px;
         vertical-align: top;
      }
      &__social {
         @include f(start, start);
         :last-child{
            margin-right: 0px;
         }
      }
      &__social-btn {
         @include f(center, center);
         background-color: white;
         border-radius: 50%;
         height: 40px;
         width: 40px;
         margin-right: 15px;
      }
   }
     
}
@media (max-width: 768px) {
    .header{
        a{
            font-size: 11px;
        }
        a[href^="tel:"] {
            display: none;
        }
         &__firstLine{
           @include f(space-between, center);
           margin-bottom: 20px;
        }
           
        &__about{
           margin-left: 0px;
           a {
            margin-right: 10px;
            }
        }
        &__nav-button{
            font-size: 12px;
        }
        &__nav-buttonMore{
            font-size: 12px;
        }
    }
}
